$prefix: '.rise-blocks';

/* Shadow */
#{$prefix}-shadow-enable {
    .block-editor-block-list__block {
        box-shadow: 0px 0 16px 0 rgba(0, 0, 0, 0.05);
    }
}

/* Grid */
#{$prefix}-block-lists>div>div {
    height: 100%;
}

/* Block top gap */
#{$prefix}-icon-blocks-wrapper {
    .wp-block {
        margin-top: 70px;
    }
}

/* Core style override */
#{$prefix}-block-lists .block-editor-block-list__block {
    padding-left: 28px;
    padding-right: 28px;
}

/* Editor style */
#{$prefix}-icon-blocks-wrapper {
    .block-list-appender {
        flex: 100%;
    }

    >.block-editor-block-list__block>.block-editor-block-list__block-edit,
    .block-editor-block-list__layout>.block-editor-block-list__block>.block-editor-block-list__block-edit {
        margin-top: 0;
        margin-bottom: 0;
    }
}

#{$prefix}-items-per-row-1{
    .block-editor-block-list__layout{
        display: flex;
        flex-wrap: wrap;
    }
    .rise-blocks-icon-box{
        max-width: 100%;
        flex: 0 0 100%;
    }
}

#{$prefix}-items-per-row-2{
    .block-editor-block-list__layout{
        display: flex;
        flex-wrap: wrap;
    }
    .rise-blocks-icon-box{
        max-width: 50%;
        flex: 0 0 50%;
    }
}

#{$prefix}-items-per-row-3{
    .block-editor-block-list__layout{
        display: flex;
        flex-wrap: wrap;
    }
    .rise-blocks-icon-box{
        max-width: 33.33%;
        flex: 0 0 33.33%;
    }
}

#{$prefix}-items-per-row-4{
    .block-editor-block-list__layout{
        display: flex;
        flex-wrap: wrap;
    }
    .rise-blocks-icon-box{
        max-width: 25%;
        flex: 0 0 25%;
    }
}

#{$prefix}-items-per-row-5{
    .block-editor-block-list__layout{
        display: flex;
        flex-wrap: wrap;
    }
    .rise-blocks-icon-box{
        max-width: 20%;
        flex: 0 0 20%;
    }
}